.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 5rem 0;
  align-items: center;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .navegacion {
    flex-direction: row;
    width: auto;
  }
}
.navegacion__link {
  font-size: 2rem;
  font-weight: 700;
  color: #000;
}
.navegacion__link--registrar {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.navegacion__link--registrar:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .navegacion__link--registrar {
    width: auto;
  }
}

@media (min-width: 768px) {
  .formulario__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formulario__contenido {
  background-color: #e8505b;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  border-radius: 1rem 2rem;
}
.formulario__heading {
  color: #FFFFFF;
  text-align: center;
}
.formulario__campo {
  margin-bottom: 1rem;
}
.formulario__label {
  display: block;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 1rem 0;
}
.formulario__input {
  padding: 1.5rem;
  width: 100%;
  border: none;
}
.formulario__submit {
  margin-top: 2rem;
  background-color: #455a64;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .formulario__submit {
    width: 100%;
  }
}
.formulario__imagen--contenedor {
  display: flex;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .formulario__imagen--contenedor {
    margin-top: 0;
  }
}
.formulario__imagen {
  width: 30rem;
}
@media (min-width: 1024px) {
  .formulario__imagen {
    width: 100%;
  }
}

.pasos {
  margin-top: 5rem;
}
.pasos__wave {
  margin-bottom: -0.1rem;
}
.pasos__contenido {
  background-color: #e8505b;
}
.pasos__heading {
  text-align: center;
  color: #FFFFFF;
}
.pasos__heading--mod {
  display: block;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .pasos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.pasos__imagen {
  transition: transform 300ms ease-in-out;
}
.pasos__imagen:hover {
  transform: rotate(-4deg) scale(1.2);
}

.testimoniales {
  padding: 5rem;
}
.testimoniales__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.testimonial {
  background-color: #455a64;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  color: #FFFFFF;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .testimonial {
    margin-bottom: 0;
  }
  .testimonial:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonial:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-column: 1/3;
  }
  .testimonial:nth-child(5) {
    grid-column: 3/5;
  }
}
.testimonial__header {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.testimonial__imagen {
  flex-basis: 10rem;
  border-radius: 50%;
  overflow: hidden;
}
.testimonial__autor {
  color: #FFFFFF;
  font-weight: bold;
  margin: 0.5rem 0;
}
.testimonial__texto {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0 0 0;
  padding-left: 4rem;
  position: relative;
}
.testimonial__texto::before {
  content: open-quote;
  font-size: 10rem;
  position: absolute;
  left: 0;
  top: -4rem;
  opacity: 0.2;
  font-family: "trebuchet", "sans-serif";
}